#BaseView{background:#fff;display:flex;width:100vw;height:100vh}#BaseView #NavigationContent{width:200px}#BaseView #MainContent{width:calc(100vw - 200px);background:#f7f7f9}#BaseView>>>header.base-header{font-family:"Lato"}#BaseView>>>header.base-header h3{font-size:1.75rem;font-weight:lighter;padding:20px 30px 0px 50px}#BaseView>>>header.base-header nav.navbar{display:flex;margin:0px 0 0 0px;border-radius:0px;padding-left:50px;padding-bottom:0px}#BaseView>>>header.base-header nav.navbar nav{text-transform:uppercase;font-family:"League Spartan";font-size:.75rem;color:#818a91;margin-right:50px;cursor:pointer;padding:10px 5px;border-bottom:1px solid transparent;display:flex}#BaseView>>>header.base-header nav.navbar nav .nav-item-icon{margin-top:-5px}#BaseView>>>header.base-header nav.navbar nav:hover,#BaseView>>>header.base-header nav.navbar nav.active{border-bottom:2px solid #00A550}#BaseView>>>header.base-header nav.navbar nav.active{color:#55595c}#BaseView>>>section.base-section{background:#fff}
